.about {
	padding: 45px 0 40px;
	color: $dark;
	.container {
		background: white;
		max-width: 1510px;
		@include flex(row, between, start, wrap);
		overflow: hidden;
		border-radius: 10px;
		box-shadow: $container-shadow; }
	.block-title {
		margin: 0 0 37px 0; }
	&__img {
		max-height: 440px;
		height: 100%;
		display: block;
		max-width: 641px;
		width: auto; }
	.text-container {
		margin: 45px 50px;
		width: calc( 100% - 741px );
		overflow: auto;
		font-family: 'UniBook', sans-serif;
		line-height: 1.6; } }

